export default function AddStudentModel(){
    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-6 w-full max-w-6xl max-h-[90vh] overflow-y-auto">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-bold text-gray-800">
                  {showEditStudentModal ? 'Edit Student' : 'Add New Student'}
                </h2>
                <button
                  onClick={() => {
                    setShowAddStudentModal(false);
                    setShowEditStudentModal(false);
                    setEditingStudent(null);
                    resetForm();
                  }}
                  className="text-gray-500 hover:text-gray-700"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <form onSubmit={(e) => { e.preventDefault(); showEditStudentModal ? handleUpdateStudent() : handleAddStudent(); }}>
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                  {/* Left Column - Personal & Contact Information */}
                  <div className="space-y-6">
                    {/* Personal Information */}
                    <div className="space-y-4">
                      <h3 className="text-lg font-semibold text-gray-700 border-b pb-2">Personal Information</h3>
                      
                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">First Name *</label>
                          <input
                            type="text"
                            name="first_name"
                            value={studentFormData.first_name}
                            onChange={handleInputChange}
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.first_name ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.first_name && <p className="text-red-500 text-xs mt-1">{formErrors.first_name}</p>}
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Last Name *</label>
                          <input
                            type="text"
                            name="last_name"
                            value={studentFormData.last_name}
                            onChange={handleInputChange}
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.last_name ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.last_name && <p className="text-red-500 text-xs mt-1">{formErrors.last_name}</p>}
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Date of Birth</label>
                          <input
                            type="date"
                            name="date_of_birth"
                            value={studentFormData.date_of_birth}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Gender</label>
                          <select
                            name="gender"
                            value={studentFormData.gender}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          >
                            <option value="">Select Gender</option>
                            <option value="Male">Male</option>
                            <option value="Female">Female</option>
                            <option value="Other">Other</option>
                          </select>
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Phone Number *</label>
                          <input
                            type="tel"
                            name="phone"
                            value={studentFormData.phone}
                            onChange={handleInputChange}
                            placeholder="10-digit number"
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.phone ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.phone && <p className="text-red-500 text-xs mt-1">{formErrors.phone}</p>}
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Alternate Phone</label>
                          <input
                            type="tel"
                            name="alternate_phone"
                            value={studentFormData.alternate_phone}
                            onChange={handleInputChange}
                            placeholder="10-digit number"
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">College Email *</label>
                        <input
                          type="email"
                          name="college_email"
                          value={studentFormData.college_email}
                          onChange={handleInputChange}
                          className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                            formErrors.college_email ? 'border-red-500' : 'border-gray-300'
                          }`}
                        />
                        {formErrors.college_email && <p className="text-red-500 text-xs mt-1">{formErrors.college_email}</p>}
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Personal Email</label>
                        <input
                          type="email"
                          name="personal_email"
                          value={studentFormData.personal_email}
                          onChange={handleInputChange}
                          className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                            formErrors.personal_email ? 'border-red-500' : 'border-gray-300'
                          }`}
                        />
                        {formErrors.personal_email && <p className="text-red-500 text-xs mt-1">{formErrors.personal_email}</p>}
                      </div>
                    </div>

                    {/* Address Information */}
                    <div className="space-y-4">
                      <h3 className="text-lg font-semibold text-gray-700 border-b pb-2">Address Information</h3>
                      
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Permanent Address</label>
                        <textarea
                          name="permanent_address"
                          value={studentFormData.permanent_address}
                          onChange={handleInputChange}
                          rows="3"
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                        />
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">City</label>
                          <input
                            type="text"
                            name="permanent_city"
                            value={studentFormData.permanent_city}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">State</label>
                          <input
                            type="text"
                            name="permanent_state"
                            value={studentFormData.permanent_state}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Right Column - Academic & Professional Information */}
                  <div className="space-y-6">
                    {/* Academic Information */}
                    <div className="space-y-4">
                      <h3 className="text-lg font-semibold text-gray-700 border-b pb-2">Academic Information</h3>
                      
                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Enrollment Number *</label>
                          <input
                            type="text"
                            name="enrollment_number"
                            value={studentFormData.enrollment_number}
                            onChange={handleInputChange}
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.enrollment_number ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.enrollment_number && <p className="text-red-500 text-xs mt-1">{formErrors.enrollment_number}</p>}
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Registration Number *</label>
                          <input
                            type="text"
                            name="registration_number"
                            value={studentFormData.registration_number}
                            onChange={handleInputChange}
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.registration_number ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.registration_number && <p className="text-red-500 text-xs mt-1">{formErrors.registration_number}</p>}
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Department *</label>
                          <input
                            type="text"
                            name="department"
                            value={studentFormData.department}
                            onChange={handleInputChange}
                            placeholder="e.g., B.Tech"
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.department ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.department && <p className="text-red-500 text-xs mt-1">{formErrors.department}</p>}
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Branch *</label>
                          <input
                            type="text"
                            name="branch"
                            value={studentFormData.branch}
                            onChange={handleInputChange}
                            placeholder="e.g., CSE, IT, ECE"
                            className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 ${
                              formErrors.branch ? 'border-red-500' : 'border-gray-300'
                            }`}
                          />
                          {formErrors.branch && <p className="text-red-500 text-xs mt-1">{formErrors.branch}</p>}
                        </div>
                      </div>

                      <div className="grid grid-cols-3 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Batch Year</label>
                          <input
                            type="number"
                            name="batch_year"
                            value={studentFormData.batch_year}
                            onChange={handleInputChange}
                            min="2020"
                            max="2030"
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Current Semester</label>
                          <select
                            name="current_semester"
                            value={studentFormData.current_semester}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          >
                            {[1,2,3,4,5,6,7,8].map(sem => (
                              <option key={sem} value={sem}>{sem}</option>
                            ))}
                          </select>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">CGPA</label>
                          <input
                            type="number"
                            step="0.01"
                            min="0"
                            max="10"
                            name="cgpa"
                            value={studentFormData.cgpa}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                      </div>

                      <div className="grid grid-cols-3 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">10th Percentage</label>
                          <input
                            type="number"
                            step="0.01"
                            min="0"
                            max="100"
                            name="class_10_percentage"
                            value={studentFormData.class_10_percentage}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">12th Percentage</label>
                          <input
                            type="number"
                            step="0.01"
                            min="0"
                            max="100"
                            name="class_12_percentage"
                            value={studentFormData.class_12_percentage}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Backlogs</label>
                          <input
                            type="number"
                            min="0"
                            name="backlogs"
                            value={studentFormData.backlogs}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                      </div>
                    </div>

                    {/* Professional Information */}
                    <div className="space-y-4">
                      <h3 className="text-lg font-semibold text-gray-700 border-b pb-2">Professional Information</h3>
                      
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Resume URL</label>
                        <input
                          type="url"
                          name="resume_url"
                          value={studentFormData.resume_url}
                          onChange={handleInputChange}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                        />
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">LinkedIn URL</label>
                          <input
                            type="url"
                            name="linkedin_url"
                            value={studentFormData.linkedin_url}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">GitHub URL</label>
                          <input
                            type="url"
                            name="github_url"
                            value={studentFormData.github_url}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">PS2 Company</label>
                          <input
                            type="text"
                            name="ps2_company_name"
                            value={studentFormData.ps2_company_name}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">PS2 Project Title</label>
                          <input
                            type="text"
                            name="ps2_project_title"
                            value={studentFormData.ps2_project_title}
                            onChange={handleInputChange}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                          />
                        </div>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">PS2 Certificate URL</label>
                        <input
                          type="url"
                          name="ps2_certificate_url"
                          value={studentFormData.ps2_certificate_url}
                          onChange={handleInputChange}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Placement Status</label>
                        <select
                          name="placement_status"
                          value={studentFormData.placement_status}
                          onChange={handleInputChange}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                        >
                          <option value="placed">Placed</option>
                          <option value="unplaced">Unplaced</option>
                          <option value="in-process">Higher Studies</option>
                          <option value="ineligible">Entrepreneurship</option>
                          <option value="not_interested">Debarred</option>
                        </select>
                      </div>  
                    </div>
                  </div>
                </div>

                <div className="flex justify-end space-x-3 pt-6 border-t mt-6">
                  <button
                    type="button"
                    onClick={() => {
                      setShowAddStudentModal(false);
                      setShowEditStudentModal(false);
                      setEditingStudent(null);
                      resetForm();
                    }}
                    className="px-6 py-2 text-gray-600 border border-gray-300 rounded-lg hover:bg-gray-50"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center space-x-2"
                  >
                    {showEditStudentModal ? <Edit className="h-4 w-4" /> : <Plus className="h-4 w-4" />}
                    <span>{showEditStudentModal ? 'Update Student' : 'Add Student'}</span>
                  </button>
                </div>
              </form>
            </div>
          </div>
    );
}